*, :before, :after {
  box-sizing: border-box;
}

body {
  color: #2a2a2a;
  font-family: Helvetica Neue, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, sans-serif;
  line-height: 1.5;
}

button, label {
  cursor: pointer;
}

input {
  font: inherit;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.alert {
  color: #2a2a2a;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  background-color: #fff;
  border: 1px solid #2a2a2a;
  border-radius: 3px;
  margin: 0;
  padding: 24px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  bottom: 20px;
  right: 20px;
  transform: translateX(calc(100% + 20px));
}

.alert.success {
  color: #fff;
  background-color: #4caf50;
  border: 1px solid #388e3c;
}

.alert.is-visible {
  visibility: visible;
  opacity: 1;
  transform: translateX(0);
}

.timer {
  text-align: center;
  color: #212121;
  border-radius: 2px;
  margin-left: auto;
  margin-right: auto;
}

.clockface {
  margin-top: 0;
  margin-bottom: 24px;
  font-family: Roboto Mono, monospace;
  font-size: 80px;
}

.timer-btn {
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #3f51b5;
  border: 0;
  border-radius: 3px;
  margin-left: 4px;
  margin-right: 4px;
  padding: 10px 20px;
  font-size: 24px;
  display: inline-flex;
}

.timer-btn.is-active {
  background-color: #2196f3;
}

.loader {
  border: 4px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin: 100px auto;
  animation: .7s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.card {
  text-align: center;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 10px;
  max-width: 300px;
  margin: 20px auto;
  font-family: Arial, sans-serif;
  transition: transform .2s, box-shadow .2s;
  overflow: hidden;
  box-shadow: 0 4px 6px #0000001a;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 10px #00000026;
}

.card-img-top img {
  width: 100%;
  height: auto;
  display: block;
}

.card-body {
  padding: 20px;
}

.card-title {
  color: #333;
  margin: 0 0 10px;
  font-size: 1.5rem;
}

.card-text {
  color: #555;
  margin: 5px 0;
  font-size: 1rem;
}

.card ul {
  margin: 10px 0 0;
  padding: 0;
  list-style: none;
}

.card ul li {
  color: #666;
  font-size: .9rem;
}

.card ul li:not(:last-child) {
  margin-bottom: 5px;
}
